  text-align: center;
  font-size: 1em;
  opacity: 0.5;
}

.contact-form div input::placeholder {
  color: #b9b8b8;
  text-align: center;
  font-size: 1em;
  opacity: 0.5;
}

.contact-form div input:focus {
  border: none;
}

.contact-form div button {
  color: white;
  background: #150734;
  border: 2px solid #f7f2f2;
  border-radius: 20px;
}

.contact-form div button:hover {
  transform: scale(1.1);
  cursor: pointer;
  border: 2px solid #deb992;
  color: #deb992;
}

.footer {
  background: #150734;
  display: flex;
  justify-content: center;
  padding: 30px;
  color: #5e5edf;
}

.footer i {
